/* inter-regular - latin */
@font-face {
  font-display: swap;
  font-family: 'Inter';
  font-style: normal;
  font-weight: 400;
  src: url('./assets/fonts/inter-v13-latin-regular.woff2') format('woff2'),
    url('./assets/fonts/inter-v13-latin-regular.ttf') format('truetype');
}
/* inter-500 - latin */
